import { Amenities } from '@models';
import { IServiceType } from '@interfaces';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Observable, Subscription } from 'rxjs';
import { RestaurantService } from '@services';
import { RestuarantServiceTypePayload } from '@payloads';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { ServiceType, ResponseDetail } from '@models';
import { map, mergeMap } from 'rxjs/operators';

@Component({
  selector: 'app-service-type',
  templateUrl: './service-type.component.html',
  styleUrls: ['./service-type.component.scss']
})
export class ServiceTypeComponent implements OnInit, OnDestroy {
  serviceTypeForm: FormGroup = this.fb.group({
    type: new FormControl([])
  });

  serviceTypes: Array<ServiceType> = [];
  currentServiceTypes: Array<number>;
  restaurantId: number = parseInt(localStorage.getItem('restaurantId'), 10);
  restaurantSubscription = Subscription.EMPTY;
  amenitiesSubscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private restaurantService: RestaurantService,
    private snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.restaurantSubscription = this.getRestaurantDetails(this.restaurantId)
      .pipe(
        mergeMap((response: ResponseDetail): any => {
          this.currentServiceTypes = response.restaurant.type
            .filter((pType: IServiceType) => pType.active === true)
            .map((pType: IServiceType) => pType.id);

          this.serviceTypeForm.patchValue({ type: this.currentServiceTypes });

          return this.getServiceTypes();
        })
      )
      .subscribe((response: any) => {
        this.createTypesObjects(response as Array<ServiceType>);
      });
  }

  ngOnDestroy() {
    this.restaurantSubscription.unsubscribe();
    this.amenitiesSubscription.unsubscribe();
  }

  private createTypesObjects(serviceTypes: Array<ServiceType>): void {
    const typesOptions = serviceTypes;

    typesOptions.forEach(pType => {
      const typeObject = {
        name: pType.name,
        id: pType.id,
        active: false
      };

      const isActive = this.currentServiceTypes.find(id => id === pType.id);

      if (isActive) {
        typeObject.active = true;
      }

      this.serviceTypes.push(typeObject);
    });
  }

  private getServiceTypes(): Observable<Array<ServiceType>> {
    return this.restaurantService.getAmenities().pipe(
      map((response: Amenities) => {
        const serviceTypes: Array<ServiceType> = response.types.map(
          (pType: IServiceType) => {
            return new ServiceType(pType);
          }
        );

        return serviceTypes;
      })
    );
  }

  onTypeCheck(type: IServiceType): void {
    type.active = !type.active;

    let currentSelectedTypes = this.serviceTypeForm.get('type').value;

    currentSelectedTypes = this.onTypeSelection(type, currentSelectedTypes);

    this.serviceTypeForm.patchValue({
      type: currentSelectedTypes
    });
  }

  onSubmit(): void {
    const serviceTypePayload = new RestuarantServiceTypePayload(
      this.serviceTypeForm
    );

    this.amenitiesSubscription = this.restaurantService
      .setRestaurantAmenities(serviceTypePayload.toJson(), this.restaurantId)
      .subscribe(() => this.openSnackBar('Cambios guardados', 'Entendido'));
  }

  private onTypeSelection(
    type: IServiceType,
    currentTypes: Array<number>
  ): Array<number> {
    const isTypeSelected = currentTypes.includes(type.id);

    if (isTypeSelected) {
      const index = currentTypes.indexOf(type.id);

      currentTypes.splice(index, 1);
    } else {
      currentTypes.push(type.id);
    }

    return currentTypes;
  }

  private getRestaurantDetails(id: number): Observable<ResponseDetail> {
    return this.restaurantService.getRestaurantDetail(id);
  }

  private openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000
    });
  }
}
